import React, { useLayoutEffect, useRef } from 'react';
import { useModeSelector } from 'use-light-switch';
import config from '../website-config'

interface UtterancesMediaMode {
  theme: string
}

const Comment: React.FC = React.memo(()  => {
  const containerRef = useRef<HTMLDivElement>(null);

  const utterancesMediaMode: UtterancesMediaMode = useModeSelector({
    light: {theme: 'github-light'},
    dark: {theme: 'github-dark'},
    unset: {theme: 'unse'}
  })!;

  useLayoutEffect(() => {
    if (!config.utterancesRepo) {
      return;
    }

    const attributes = {
      src: 'https://utteranc.es/client.js',
      repo: config.utterancesRepo,
      'issue-term': 'pathname',
      label: 'comment',
      theme: utterancesMediaMode.theme,
      crossOrigin: 'anonymous',
      async: 'true',
    };

    const utterances = document.createElement('script');
    Object.entries(attributes).forEach(([key, value]) => {
      utterances.setAttribute(key, value);
    });

    containerRef.current?.appendChild(utterances);
  }, []);

  useLayoutEffect(() => {
    const message = {
      type: 'set-theme',
      theme: utterancesMediaMode.theme
    };
    const utterances = document.querySelector('iframe')?.contentWindow;
    utterances?.postMessage(message, 'https://utteranc.es');
  }, [utterancesMediaMode]);

  return <div ref={containerRef} />;
});

export default Comment;
